import { createSlice } from "@reduxjs/toolkit";
// utils
import axios from "../../utils/axios";
//
import { dispatch } from "../store";

// ----------------------------------------------------------------------

const initialState = {
  isLoading: false,
  error: null,
  categories: [],
};

const slice = createSlice({
  name: "category",
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    // GET EVENTS
    getCategories(state, action) {
      state.isLoading = false;
      state.categories = action.payload;
    },
  },
});

// Reducer
export default slice.reducer;

// Actions
export const { getCategories } = slice.actions;

// ----------------------------------------------------------------------

export function GetCategories() {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get("api/v1/category");
      dispatch(slice.actions.getCategories(response.data.data));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function createCategory(newEvent) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.post("/api/v1/category/add", newEvent);
      dispatch(GetCategories());
      return response;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function updateCategory(categoryId, updateData) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.put(
        `/api/v1/category/${categoryId}`,
        updateData
      );
      dispatch(GetCategories());
      return response;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function deleteCategory(eventId) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const deletedItem = await axios.delete(`/api/v1/category/${eventId}`);
      dispatch(GetCategories());
      return deletedItem;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------
