import { createSlice } from "@reduxjs/toolkit";
// utils
import axios from "../../utils/axios";
//
import { dispatch } from "../store";

// ----------------------------------------------------------------------

const initialState = {
  isLoading: false,
  error: null,
  categoriesCount: [],
  brandsCount: [],
  couponsCount: [],
};

const slice = createSlice({
  name: "count",
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    // GET EVENTS
    Count(state, action) {
      state.isLoading = false;
      state.categoriesCount = action.payload.category;
      state.brandsCount = action.payload.brand;
      state.couponsCount = action.payload.coupons;
    },
  },
});

// Reducer
export default slice.reducer;

// Actions
export const { Count } = slice.actions;

// ----------------------------------------------------------------------

export function GetCount() {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get("api/v1/count");
      dispatch(slice.actions.Count(response.data.data));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}
