import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import CircularProgress from "@mui/material/CircularProgress";
import FileCopyIcon from "@mui/icons-material/FileCopy";
import { Button } from "@mui/material";
import { updateSiteVersion } from "src/redux/slices/wfUser";
import { LoadingButton } from "@mui/lab";
import { useSnackbar } from "notistack";

const Sitemap = () => {
  const dispatch = useDispatch();

  const siteMap = useSelector(
    (state) => state?.collection?.sitemap?.data?.sitemap
  );
  const [status, setStatus] = useState(null);
  const [buttonsDisabled, setButtonsDisabled] = useState(false);
  const isLoading = useSelector((state) => state.collection.isLoading);
  const domainId = localStorage.getItem("domainId");
  const pageName = localStorage.getItem("pageName");
  const siteId = localStorage.getItem("siteId");
  const collectionIds = localStorage.getItem("collectionIds");
  const [isSubmitting, setIsSubmitting] = useState(false); // State to store the search query
  const { enqueueSnackbar } = useSnackbar();

  const handleDraftButtonClick = async () => {
    setIsSubmitting(true);
    // Handle draft button click logic
    const ress = await dispatch(
      updateSiteVersion(
        true,
        false,
        siteMap,
        domainId,
        siteId,
        collectionIds,
        pageName
      )
    );
    if (ress.success) {
      setStatus("draft");
      enqueueSnackbar("Sitemap is saved as draft");
      setIsSubmitting(false);
      setButtonsDisabled(true); // Disable both buttons
    }
  };
  const handlePublishButtonClick = async () => {
    setIsSubmitting(true);
    // Handle draft button click logic
    const ress = await dispatch(
      updateSiteVersion(
        false,
        true,
        siteMap,
        domainId,
        siteId,
        collectionIds,
        pageName
      )
    );
    if (ress.success) {
      setStatus("published");
      enqueueSnackbar("Sitemap is published");
      setIsSubmitting(false);
      setButtonsDisabled(true); // Disable both buttons
    }
  };
  const handleCopyToClipboard = () => {
    const el = document.createElement("textarea");
    el.value = siteMap;
    el.setAttribute("readonly", "");
    el.style.position = "absolute";
    el.style.left = "-9999px";
    document.body.appendChild(el);
    const selected =
      document.getSelection().rangeCount > 0
        ? document.getSelection().getRangeAt(0)
        : false;
    el.select();
    document.execCommand("copy");
    document.body.removeChild(el);
    if (selected) {
      document.getSelection().removeAllRanges();
      document.getSelection().addRange(selected);
    }
    alert("Sitemap code copied to clipboard!");
  };

  return (
    <div style={{ display: "flex", flexDirection: "column", height: "100%" }}>
      <div
        style={{
          flex: 1,
          overflowY: "scroll",
          backgroundColor: "#f0f0f0",
          borderRadius: "10px",
          boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
          margin: "0 10px",
          WebkitOverflowScrolling: "touch",
          scrollbarWidth: "thin",
          scrollbarColor: "#7a7a7a #f0f0f0",
        }}
      >
        <div>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              position: "sticky",
              top: 0,
              backgroundColor: "#f0f0f0",
              padding: "10px",
              width: "100%",
              borderBottom: "1px solid #ccc",
            }}
          >
            <h2>SiteMap</h2>
            <FileCopyIcon
              style={{ cursor: "pointer" }}
              onClick={handleCopyToClipboard}
            />
          </div>
          {isLoading ? (
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                height: "100%",
              }}
            >
              <CircularProgress
                sx={{
                  width: 64,
                  height: 64,
                  margin: "auto", // Center the CircularProgress both vertically and horizontally
                  display: "block",
                  marginTop: "90px",
                }}
              />
            </div>
          ) : (
            <pre
              style={{
                fontFamily: "Courier New, monospace",
                color: "#2e2e2e",
                backgroundColor: "#f1f1f1",
                padding: "15px",
                borderRadius: "5px",
              }}
            >
              {siteMap}
            </pre>
          )}
        </div>
      </div>
      <div
        style={{ display: "flex", justifyContent: "flex-end", padding: "10px" }}
      >
        <LoadingButton
          disabled={!siteMap || buttonsDisabled}
          color="inherit"
          variant="outlined"
          size="medium"
          onClick={handleDraftButtonClick}
          loading={isSubmitting}
          sx={{
            m: 1.5,
            color: "white",
            bgcolor: "blue",
            color: "white",
            "&:hover": {
              borderColor: "lightblue",
              bgcolor: "lightblue",
            },
          }}
        >
          {status === "draft" ? "Saved as Draft!!" : "Save as Draft"}
        </LoadingButton>
        <LoadingButton
          disabled={!siteMap || buttonsDisabled}
          color="inherit"
          variant="outlined"
          size="medium"
          onClick={handlePublishButtonClick}
          loading={isSubmitting}
          sx={{
            m: 1.5,
            color: "white",
            bgcolor: "blue",
            color: "white",
            "&:hover": {
              borderColor: "lightblue",
              bgcolor: "lightblue",
            },
          }}
        >
          {status === "published" ? "Mark as Published!!" : "Publish"}
        </LoadingButton>
      </div>
    </div>
  );
};

export default Sitemap;
