export function getforgotPassword(email) {
  localStorage.setItem("email", email);
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const requestBody = {
        email: email,
      };

      const response = await axios.post(
        `api/v1/user/forgetPassword`,
        requestBody
      );

      dispatch(slice.actions.forgotPassword(response.data)); // Dispatch the response data instead of the entire response
      return response.data;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
      throw error; // Rethrow the error to be caught where this function is called
    }
  };
}

export function verifyOTP(otp) {
  const email = localStorage.getItem("email");
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const requestBody = {
        email: email,
        otp: otp,
      };
      const response = await axios.post(`api/v1/user/verifyOTP`, requestBody);
      return response.data;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
      throw error;
    }
  };
}

export function createPassword(password, confirm_password) {
  const email = localStorage.getItem("email");
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const requestBody = {
        email: email,
        password: password,
        confirm_password: confirm_password,
      };
      const response = await axios.post(
        `api/v1/user/resetPassword`,
        requestBody
      );
      return response.data;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
      throw error;
    }
  };
}
