import {
  Card,
  Button,
  Table,
  Divider,
  TableRow,
  TableBody,
  TableCell,
  TableHead,
  IconButton,
  TableContainer,
  useTheme,
  Box,
} from "@mui/material";
import { sentenceCase } from "change-case";
import { useSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { deleteBrand, ListBrands } from "src/redux/slices/brand";
import { dispatch, useSelector } from "src/redux/store";
import DeleteIcon from "@mui/icons-material/Delete";
import ModeIcon from "@mui/icons-material/Mode";
import HeaderBreadcrumbs from "src/components/HeaderBreadcrumbs";
import Iconify from "src/components/Iconify";
import Scrollbar from "src/components/Scrollbar";
import Image from "src/components/Image";
import Label from "src/components/Label";
import DeleteConfirmationBox from "src/pages/dashboard/DeleteConfirmation";

export default function AppNewBrand({ callBack, updateForm }) {
  const theme = useTheme();
  const [rowToDelete, setRowToDelete] = useState(null);
  const [showConfirmationBox, setShowConfirmationBox] = useState(false);

  const { enqueueSnackbar } = useSnackbar();
  useEffect(() => {
    dispatch(ListBrands());
  }, []);

  const data = useSelector((state) => state.brand.brandList);
  const error = useSelector((state) => state.brand.error);

  useEffect(() => {
    if (error) {
      enqueueSnackbar(error.message, { variant: "error" });
    }
  }, [error]);

  const deleteBrands = async (id) => {
    const deletedBrand = dispatch(deleteBrand(id)); // typo here
    if (deletedBrand.status) {
    }
    return deletedBrand;
  };

  const handleDeleteClick = (id) => {
    setRowToDelete(id);
    setShowConfirmationBox(true);
  };

  const handleDeleteConfirmation = async () => {
    const deletedBrand = await deleteBrands(rowToDelete);
    if (deletedBrand.status) {
      setShowConfirmationBox(false);
      enqueueSnackbar("Brand successfully deleted!");
    } else {
      setShowConfirmationBox(false);
    }
  };

  return (
    <Card>
      <HeaderBreadcrumbs
        heading={"Brands List"}
        links={[]}
        sx={{ m: 2, mt: 1, mb: 0 }}
        action={
          <Button
            sx={{ padding: "6px 10px", fontSize: "12px", minWidth: "10px" }}
            variant="contained"
            startIcon={<Iconify icon={"eva:plus-fill"} />}
            onClick={() => {
              callBack();
            }}
          >
            Add Brand
          </Button>
        }
      />

      <Scrollbar>
        <TableContainer sx={{ height: "600px", overflow: "auto" }}>
          <Table>
            <TableHead sx={{ position: "sticky", top: 0, zIndex: 10 }}>
              <TableRow>
                <TableCell sx={{ padding: 0.5, width: "8px" }}>Index</TableCell>
                <TableCell sx={{ padding: 0.5, width: "8px" }}>
                  Brand Logo
                </TableCell>
                <TableCell sx={{ padding: 0.5, width: "8px" }}>
                  Brand Name
                </TableCell>
                <TableCell sx={{ padding: 0.5, width: "8px" }}>State</TableCell>
                {/* <TableCell sx={{ padding: 0.5, width: "8px" }}>City</TableCell> */}
                <TableCell sx={{ padding: 0.5, width: "8px" }}>
                  Categories
                </TableCell>
                <TableCell sx={{ padding: 0.5, width: "8px" }}>
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {data?.map((row, index) => (
                <TableRow key={row?.id}>
                  <TableCell sx={{ padding: 0.5 }}> {index + 1}</TableCell>
                  <TableCell sx={{ padding: 0.5 }}>
                    <Image
                      src={row?.logo}
                      alt="brand"
                      sx={{
                        background: "#d8dad8",
                        border: "1px solid transparent",
                        borderRadius: 1.5,
                        width: "50px",
                        height: "50px",
                        "& .wrapper": {
                          display: "block !important",
                          height: "inherit",
                          "& img": {
                            height: "inherit",
                            minHeight: "inherit",
                          },
                        },
                      }}
                    />
                  </TableCell>
                  <TableCell sx={{ padding: 0.5 }}>{row?.brand_name}</TableCell>
                  <TableCell sx={{ padding: 0.5 }}>
                    {row?.states.length <= 5 ? (
                      // Render all state if there are 5 or fewer
                      row?.states.map((item) => item.name).join(",")
                    ) : (
                      // Render the first 5 states followed by a "+X" label for the remaining count
                      <>
                        {row?.states
                          .slice(0, 5)
                          .map((item) => item.name)
                          .join(",")}
                        ,+{row?.states.length - 5}
                      </>
                    )}
                  </TableCell>
                  <TableCell
                    sx={{ whiteSpace: "nowrap", maxWidth: 200, padding: 0.5 }}
                  >
                    <Box gap={1} sx={{ display: "flex", flexWrap: "wrap" }}>
                      {row?.categories.length <= 5 ? (
                        // Render all state if there are 5 or fewer
                        row?.categories?.map((item, index) => item?.name)
                      ) : (
                        // Render the first 5 states followed by a "+X" label for the remaining count
                        <>
                          {row?.categories
                            .slice(0, 5)
                            .map((item) => item?.name)
                            .join(",")}
                          ,+{row?.categories.length - 5}
                        </>
                      )}
                    </Box>
                  </TableCell>
                  <TableCell sx={{ padding: 0.5 }}>
                    <IconButton
                      style={{ paddingLeft: 0 }}
                      onClick={() => {
                        updateForm({
                          id: row?.id,
                          brand_name: row?.brand_name,
                          state: row?.states,
                          city: row?.cities,
                          category: row?.categories,
                          logo: row?.logo,
                        });
                      }}
                    >
                      <ModeIcon />
                    </IconButton>
                    <IconButton
                      style={{ paddingLeft: 0 }}
                      onClick={() => handleDeleteClick(row.id)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Scrollbar>

      <Divider />
      {showConfirmationBox && (
        <DeleteConfirmationBox
          onDelete={() => handleDeleteConfirmation()}
          onCancel={() => setShowConfirmationBox(false)}
          rowToDelete={rowToDelete}
          type="brand"
        />
      )}
    </Card>
  );
}
