import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { deleteCategory, GetCategories } from "src/redux/slices/category";
import { useTheme } from "@mui/material/styles";
import DeleteIcon from "@mui/icons-material/Delete";
import ModeIcon from "@mui/icons-material/Mode";
import {
  Card,
  Button,
  Table,
  Divider,
  TableRow,
  TableBody,
  TableCell,
  TableHead,
  IconButton,
  TableContainer,
} from "@mui/material";
import HeaderBreadcrumbs from "src/components/HeaderBreadcrumbs";
import Scrollbar from "src/components/Scrollbar";
import Iconify from "src/components/Iconify";
import { useSnackbar } from "notistack";
import DeleteConfirmationBox from "src/pages/dashboard/DeleteConfirmation";

export default function AppNewCategory({ callBack, updateForm }) {
  const theme = useTheme();
  const dispatch = useDispatch();
  const [rowToDelete, setRowToDelete] = useState(null);
  const [showConfirmationBox, setShowConfirmationBox] = useState(false);
  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    dispatch(GetCategories());
  }, []);
  const categories = useSelector((state) => state.category.categories);
  const error = useSelector((state) => state.category.error);

  useEffect(() => {
    if (error) {
      enqueueSnackbar(error.message, { variant: "error" });
    }
  }, [error]);

  const deleteCategories = async (id) => {
    const deletedCategories = dispatch(deleteCategory(id)); // typo here
    if (deletedCategories.status) {
    }
    return deletedCategories;
  };

  const handleDeleteClick = (id) => {
    setRowToDelete(id);
    setShowConfirmationBox(true);
  };

  const handleDeleteConfirmation = async () => {
    const deletedCoupon = await deleteCategories(rowToDelete);
    if (deletedCoupon.status) {
      setShowConfirmationBox(false);
      enqueueSnackbar("Coupon successfully deleted!");
    } else {
      setShowConfirmationBox(false);
    }
  };

  return (
    <Card>
      <HeaderBreadcrumbs
        heading={"Categories List"}
        links={[]}
        sx={{ m: 2, mt: 1, mb: 0 }}
        action={
          <Button
            sx={{ padding: "6px 10px", fontSize: "12px", minWidth: "10px" }}
            variant="contained"
            startIcon={<Iconify icon={"eva:plus-fill"} />}
            onClick={() => {
              callBack();
            }}
          >
            Add Category
          </Button>
        }
      />

      <Scrollbar>
        <TableContainer sx={{ height: "600px", overflow: "auto" }}>
          <Table>
            <TableHead sx={{ position: "sticky", top: 0, zIndex: 10 }}>
              <TableRow>
                <TableCell sx={{ padding: 0.5, width: "8px" }}>Index</TableCell>
                <TableCell sx={{ padding: 0.5, width: "8px" }}>
                  Category Name
                </TableCell>
                <TableCell
                  sx={{ padding: 1, width: "8px", paddingLeft: "30px" }}
                >
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {categories?.map((row, index) => (
                <TableRow key={row.id}>
                  <TableCell sx={{ padding: 0.5 }}>{index + 1}</TableCell>
                  <TableCell sx={{ padding: 0.5 }}>{row.name}</TableCell>
                  <TableCell sx={{ padding: 0.5, paddingLeft: "30px" }}>
                    <IconButton
                      style={{ paddingLeft: 0 }}
                      onClick={() => {
                        updateForm({ id: row.id, name: row.name });
                      }}
                    >
                      <ModeIcon />
                    </IconButton>
                    <IconButton
                      style={{ paddingLeft: 0 }}
                      onClick={() => handleDeleteClick(row.id)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Scrollbar>

      <Divider />
      {showConfirmationBox && (
        <DeleteConfirmationBox
          onDelete={() => handleDeleteConfirmation()}
          onCancel={() => setShowConfirmationBox(false)}
          rowToDelete={rowToDelete}
          type="category"
        />
      )}
    </Card>
  );
}
