import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { useTheme } from "@mui/material/styles";

import {
  Button,
  Table,
  TableRow,
  TableBody,
  TableCell,
  TableHead,
  Card,
} from "@mui/material";
import HeaderBreadcrumbs from "src/components/HeaderBreadcrumbs";
import { useSnackbar } from "notistack";
import { ListUsers } from "src/redux/slices/user";

export default function AppNewUser({ callBack, updateForm }) {
  const theme = useTheme();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  useEffect(() => {
    dispatch(ListUsers());
  }, []);
  const users = useSelector((state) => state.user.userList);
  const error = useSelector((state) => state.coupon.error);

  useEffect(() => {
    if (error) {
      enqueueSnackbar(error.message, { variant: "error" });
    }
  }, [error]);

  return (
    <>
      <Card>
        <HeaderBreadcrumbs
          heading={"User List"}
          links={[]}
          sx={{ m: 2, mt: 1, mb: 0 }}
        />
        <Table>
          <TableHead
            sx={{
              position: "sticky",
              top: 0,
              zIndex: 999,
              background: theme.palette.background.paper,
            }}
          >
            <TableRow>
              <TableCell sx={{ padding: 1, width: "16%" }}>Index</TableCell>
              <TableCell sx={{ padding: 1, width: "28%" }}>User Name</TableCell>
              <TableCell sx={{ padding: 1, width: "28%" }}>
                Phone Number
              </TableCell>
              <TableCell sx={{ padding: 1, width: "28%" }}>City</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {users?.map((row, index) => (
              <TableRow key={row?.id}>
                <TableCell sx={{ padding: 1 }}>{index + 1}</TableCell>
                <TableCell sx={{ padding: 1 }}>
                  {console.log("ref", row)}
                  {row?.name}
                </TableCell>
                <TableCell sx={{ padding: 1 }}>{row?.phoneNumber}</TableCell>
                <TableCell sx={{ padding: 1 }}>{row?.city}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Card>
    </>
  );
}
