import { Suspense, lazy } from "react";
import { Navigate, useRoutes, useLocation } from "react-router-dom";
// layouts
// import MainLayout from "../layouts/main";
import DashboardLayout from "../layouts/dashboard";
import LogoOnlyLayout from "../layouts/LogoOnlyLayout";
// guards
// import GuestGuard from "../guards/GuestGuard";
import AuthGuard from "../guards/AuthGuard";
// import RoleBasedGuard from '../guards/RoleBasedGuard';
// config
import { PATH_AFTER_LOGIN } from "../config";
// components
import LoadingScreen from "../components/LoadingScreen";
import GeneralUser from "src/pages/dashboard/GeneralUser";
import Register from "src/pages/auth/Register";
import ResetPassword from "src/pages/auth/ResetPassword";
import Collection from "src/components/Collection";
import Sitemap from "src/components/Sitemap";
import CreatePasswordForm from "src/sections/auth/reset-password/CreatePasswordForm";
import CreatePassword from "src/pages/auth/CreatePassword";

// ----------------------------------------------------------------------

const Loadable = (Component) => (props) => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const { pathname } = useLocation();

  return (
    <Suspense
      fallback={<LoadingScreen isDashboard={pathname.includes("/dashboard")} />}
    >
      <Component {...props} />
    </Suspense>
  );
};

export default function Router() {
  return useRoutes([
    {
      path: "/",
      children: [
        { element: <Navigate to="login" replace />, index: true },
        { path: "login", element: <Login /> },
      ],
    },
    {
      path: "/",
      children: [
        { element: <Navigate to="register" replace />, index: true },
        { path: "register", element: <Register /> },
      ],
    },
    {
      path: "/",
      children: [
        { element: <Navigate to="reset-password" replace />, index: true },
        { path: "reset-password", element: <ResetPassword /> },
      ],
    },
    {
      path: "/",
      children: [
        { element: <Navigate to="create-password" replace />, index: true },
        { path: "create-password", element: <CreatePassword /> },
      ],
    },
    // Dashboard Routes
    {
      path: "dashboard",
      element: (
        <AuthGuard>
          <DashboardLayout />
        </AuthGuard>
      ),
      children: [
        { element: <Navigate to={PATH_AFTER_LOGIN} replace />, index: true },
        { path: "app", element: <GeneralApp /> },
        { path: "brand", element: <GeneralBrand /> },
        { path: "collection", element: <Collection /> },
        { path: "sitemap", element: <Sitemap /> },
        { path: "category", element: <GeneralCategory /> },
        {
          path: "coupon",
          element: <GeneralCoupon />,
        },
        { path: "user", element: <GeneralUser /> },
        { path: "add-coupon", element: <CouponForm /> },
        { path: "update-coupon/:couponId", element: <UpdateCouponForm /> },
      ],
    },
    // Main Routes
    {
      path: "*",
      element: <LogoOnlyLayout />,
      children: [
        { path: "500", element: <Page500 /> },
        { path: "404", element: <NotFound /> },
        { path: "*", element: <Navigate to="/404" replace /> },
      ],
    },
    { path: "*", element: <Navigate to="/404" replace /> },
  ]);
}

// IMPORT COMPONENTS

// Authentication
const Login = Loadable(lazy(() => import("../pages/auth/Login")));
// Dashboard
const GeneralApp = Loadable(
  lazy(() => import("../pages/dashboard/GeneralApp"))
);
const GeneralCategory = Loadable(
  lazy(() => import("../pages/dashboard/GeneralCategory"))
);
const GeneralBrand = Loadable(
  lazy(() => import("../pages/dashboard/GeneralBrand"))
);
const GeneralCoupon = Loadable(
  lazy(() => import("../pages/dashboard/GeneralCoupon"))
);
const CouponForm = Loadable(
  lazy(() => import("../pages/dashboard/CouponForm"))
);
const UpdateCouponForm = Loadable(
  lazy(() => import("../pages/dashboard/UpdateCouponForm"))
);
const Page500 = Loadable(lazy(() => import("../pages/Page500")));
const NotFound = Loadable(lazy(() => import("../pages/Page404")));
