import React from "react";
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from "@mui/material";

function DeleteConfirmationBox({ onDelete, onCancel, rowToDelete, type }) {
  const handleDelete = () => {
    onDelete(rowToDelete);
  };

  const handleCancel = () => {
    onCancel();
  };

  return (
    <Dialog open={true} onClose={handleCancel}>
      <DialogTitle>Delete Coupon</DialogTitle>
      <DialogContent>
        <DialogContentText>
          Are you sure you want to delete this {type}?
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleCancel}>No</Button>
        <Button onClick={handleDelete} autoFocus>
          Yes
        </Button>
      </DialogActions>
    </Dialog>
  );
}

export default DeleteConfirmationBox;
