import { createSlice } from "@reduxjs/toolkit";
// utils
import axios from "../../utils/axios";
//
import { dispatch } from "../store";

// ----------------------------------------------------------------------

const initialState = {
  isLoading: false,
  error: null,
  brandList: [],
  cities: [],
  categories: [],
};

const slice = createSlice({
  name: "brand",
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    // GET EVENTS
    getBrands(state, action) {
      state.isLoading = false;
      state.brandList = action.payload;
    },

    getCities(state, action) {
      state.isLoading = false;
      state.cities = action.payload;
    },

    getStates(state, action) {
      state.isLoading = false;
      state.states = action.payload;
    },

    getCategories(state, action) {
      state.isLoading = false;
      state.categories = action.payload;
    },
  },
});

// Reducer
export default slice.reducer;

// Actions
export const { getBrands } = slice.actions;

// ----------------------------------------------------------------------

export function ListBrands() {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get("api/v1/brand");
      dispatch(
        slice.actions.getBrands(
          response?.data?.data.sort(
            (a, b) => new Date(b.created_at) - new Date(a.created_at)
          )
        )
      );
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

export function ListCities() {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get("api/v1/city");
      dispatch(slice.actions.getCities(response?.data?.data));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

export function ListStates() {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get("api/v1/state");
      dispatch(slice.actions.getStates(response?.data?.data));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

export function ListCategories() {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get("api/v1/category");
      dispatch(slice.actions.getCategories(response?.data?.data));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function createBrand(newBrand) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.post("/api/v1/brand/add", newBrand);
      dispatch(ListBrands());
      return response;
    } catch (error) {
      console.log({ error });
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function updateBrand(brandId, updateData) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.put(`/api/v1/brand/${brandId}`, updateData);
      dispatch(ListBrands());
      return response;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function deleteBrand(brandId) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const deletedItem = await axios.delete(`/api/v1/brand/${brandId}`);
      dispatch(ListBrands());
      return deletedItem;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------
