import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react";
import {
  generateSiteMap,
  getPageList,
  getCollection,
  getitemList,
  getDraftDetails,
} from "src/redux/slices/collection";
// import { Select, MenuItem, FormControl, InputLabel } from "@material-ui/core";
import CircularProgress from "@mui/material/CircularProgress";
import { Button, Paper, Typography, Stack } from "@mui/material";
import styled from "styled-components";
import Select from "react-select";
import { LoadingButton } from "@mui/lab";
import { useNavigate } from "react-router";
import { PATH_DASHBOARD } from "src/routes/paths";

const SearchBar = styled.input`
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
`;

const StyledPaper = styled(Paper)`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 16px;
  background-color: #f5f5f5;
  border-radius: 12px;
`;

const Title = styled(Typography)`
  margin-bottom: 5px;
  font-weight: bold;
`;

const Content = styled(Typography)`
  margin-bottom: 5px;
  color: #666;
  font-size: 14px;
`;
const formatDate = (dateString) => {
  const options = {
    day: "numeric",
    month: "short",
    year: "numeric",
    hour: "2-digit",
    minute: "2-digit",
    second: "2-digit",
  };

  const date = new Date(dateString);
  return date.toLocaleDateString("en-US", options);
};
const DetailsComponent = ({ title, content, content2, content3 }) => {
  return (
    <>
      <StyledPaper elevation={3} style={{ width: "100%" }}>
        <Title variant="body1">name: {title}</Title>
        <Content variant="body1">slug: {content}</Content>

        {content3 && <Content variant="body1">url: {content3}</Content>}

        {content2 && (
          <Content variant="body1">last updated: {content2}</Content>
        )}

        <Stack
          direction={{ xs: "column", sm: "row" }}
          sx={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
          }}
          spacing={2}
        ></Stack>
      </StyledPaper>
    </>
  );
};

const Collection = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [selectedCollection, setSelectedCollection] = useState([]);
  const [selectedItem, setSelectedItem] = useState([]);
  const [selectedDomain, setSelectedDomain] = useState();
  const [domain, setDomain] = useState(null);
  const [allItemsSelected, setAllItemsSelected] = useState(false);
  const [selectedPages, setSelectedPages] = useState([]);
  const [isLoadingPageList, setIsLoadingPageList] = useState(false);
  const [isLoadingItemList, setIsLoadingItemList] = useState(false);
  const [searchQuery, setSearchQuery] = useState(""); // State to store the search query
  const [searchQueryPage, setSearchQueryPage] = useState(""); // State to store the search query
  const [searchQueryItem, setSearchQueryItem] = useState(""); // State to store the search query
  const [isSubmitting, setIsSubmitting] = useState(false); // State to store the search query

  const domainList = useSelector(
    (state) => state?.collection?.domain?.data?.data
  );
  const pageList = useSelector(
    (state) => state?.collection?.pageList?.data?.pageNames
  );
  const collectionList = useSelector(
    (state) => state?.collection?.collectionList?.data?.data
  );
  const itemList = useSelector(
    (state) => state?.collection?.itemList?.data?.data
  );
  const draftList = useSelector(
    (state) => state?.collection?.draft?.data?.draftDetails
  );
  const filteredCollectionList = collectionList?.collections?.filter(
    (item) =>
      item.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      item.slug.toLowerCase().includes(searchQuery.toLowerCase())
  );
  const isDomainListEmpty = !domainList || domainList?.length === 0;

  const filteredPageList = pageList?.filter((item) =>
    item.toLowerCase().includes(searchQueryPage.toLowerCase())
  );
  const containerWrapperStyle = {
    display: "flex",
    height: "500px",
  };

  const filteredItemList = itemList?.items?.flatMap((itemGroup) =>
    itemGroup?.filter(
      (item) =>
        item.name.toLowerCase().includes(searchQueryItem.toLowerCase()) ||
        item.slug.toLowerCase().includes(searchQueryItem.toLowerCase())
    )
  );
  const headingStyle = {
    position: "sticky",
    top: 0,
    backgroundColor: "#f0f0f0",
    padding: "10px",
    borderBottom: "1px solid #ccc",
  };
  const containerStyle = {
    paddingBottom: "20px",
    paddingLeft: "20px",
    paddingRight: "20px",
    overflowY: "scroll",
    backgroundColor: "#f0f0f0",
    borderRadius: "10px",
    boxShadow: "0 0 10px rgba(0, 0, 0, 0.1)",
    flexShrink: 0,
    flexGrow: 1,
    margin: "0 10px",
    WebkitOverflowScrolling: "touch",
    scrollbarWidth: "thin",
    scrollbarColor: "#7a7a7a #f0f0f0",
  };
  const centeredStyle = {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    height: "100%",
  };

  const smallWidthStyle = {
    flex: "0.7",
  };

  const bigWidthStyle = {
    flex: "1.2",
  };

  const listStyle = {
    listStyleType: "none",
    padding: "0",
  };
  const contentStyle = {
    overflowY: "auto", // Enable scrolling for the content inside the container
    height: "calc(100% - 40px)",
  };

  const listItemStyle = {
    padding: "10px 0",
  };

  const renderItemList = () => {
    if (isLoadingItemList) {
      return (
        <ul style={listStyle}>
          {filteredItemList?.flatMap(
            (itemGroup) => (
              // itemGroup?.map((itemGroup) => (
              <li key={itemGroup.id} style={listItemStyle}>
                <input
                  type="checkbox"
                  style={{
                    marginRight: "10px",
                    marginTop: "10px",
                    cursor: "pointer",
                  }}
                  checked={selectedItem?.some(
                    (selectedItem) =>
                      selectedItem.itemId === itemGroup._id &&
                      selectedItem.collectionId === itemGroup._cid
                  )}
                  onChange={() =>
                    handleItemCheck(itemGroup._id, itemGroup._cid)
                  }
                />
                {itemGroup.name}
              </li>
            )
            // ))
          )}
        </ul>
      );
    }
  };

  {
    selectedDomain ? (
      renderItemList()
    ) : (
      <p style={{ color: "red" }}>Please select a collection.</p>
    );
  }
  useEffect(async () => {
    if (selectedDomain && draftList) {
      setSelectedPages(draftList?.pageName);
      const preSelectedCollections = draftList?.collectionIds?.map(
        (item) => item?.collectionId
      );
      setSelectedCollection(preSelectedCollections);
      const preSelectedItems = draftList?.collectionIds?.flatMap((item) =>
        item?.itemIds?.map((itemId) => ({
          itemId,
          collectionId: item?.collectionId,
        }))
      );
      setSelectedItem(preSelectedItems);
    }
  }, [selectedDomain]);
  const handleReset = () => {
    setSelectedPages([]);
    setSelectedCollection([]);
    setSelectedItem([]);
  };
  useEffect(() => {
    if (selectedDomain) {
      setSelectedDomain(selectedDomain);
      setIsLoadingPageList(true);
      const domainId = selectedDomain?.value;
      dispatch(getPageList(domainId)).then(() => {
        setIsLoadingPageList(false);
      });
    }
  }, [selectedDomain]);

  const handleDomainCheck = (selectedOption) => {
    if (selectedOption) {
      setSelectedDomain(selectedOption);
      setIsLoadingPageList(true);
      const domainId = selectedOption?.value;
      dispatch(getPageList(domainId)).then(() => {
        setIsLoadingPageList(false);
      });
    }
  };
  const handleGenerateMap = () => {
    setIsSubmitting(true);
    const payload = {
      siteId: localStorage.getItem("siteId"),
      domainId: selectedDomain?.value,
      pageName: selectedPages,
      collectionIds: selectedCollection?.map((collectionId) => ({
        collectionId,
        itemIds: selectedItem
          .filter((itemId) => itemId?.collectionId === collectionId)
          ?.map((itemId) => itemId?.itemId),
      })),
      pageNames: selectedPages,
    };
    localStorage.setItem(
      "collectionIds",
      JSON.stringify(payload.collectionIds)
    );
    localStorage.setItem("pageName", JSON.stringify(payload.pageName));
    localStorage.setItem("domainId", payload.domainId);

    dispatch(generateSiteMap(payload))
      .then(() => {
        setIsLoadingPageList(false);
        setIsSubmitting(false);
        navigate(PATH_DASHBOARD.general.sitemap);
      })
      .catch((error) => {
        console.error("Error generating sitemap:", error);
      });
  };

  const handleAllSelectedItems = () => {
    if (allItemsSelected) {
      // Deselect all items
      setSelectedItem([]);
    } else {
      // Select all items
      const allItemIds = itemList?.items?.flatMap((itemGroup) =>
        itemGroup?.map((item) => ({
          itemId: item._id,
          collectionId: item._cid,
        }))
      );
      setSelectedItem(allItemIds);
    }
    setAllItemsSelected(!allItemsSelected);
  };

  const handleCollectionCheck = async (itemId) => {
    let updatedSelectedCollection = [];
    if (Array.isArray(selectedCollection) && selectedCollection.length > 0) {
      updatedSelectedCollection = [...selectedCollection];
    }
    if (updatedSelectedCollection.includes(itemId)) {
      updatedSelectedCollection = updatedSelectedCollection.filter(
        (item) => item !== itemId
      );
    } else {
      updatedSelectedCollection.push(itemId);
    }
    setSelectedCollection(updatedSelectedCollection);
    setIsLoadingItemList(true); // Set loading state when collection is being updated
  };
  useEffect(() => {
    if (draftList && domainList) {
      const defaultDomainId = draftList?.domain_id; // Assuming domain_id is the same for all drafts
      const defaultDomain = domainList?.find(
        (domain) => domain._id === defaultDomainId
      );
      if (defaultDomain) {
        setSelectedDomain({
          value: defaultDomainId,
          label: defaultDomain.name, // Set the domain name as the label
        });
      }
    }
  }, [domainList, draftList]);

  useEffect(() => {
    const siteId = localStorage.getItem("siteId");
    dispatch(getDraftDetails(siteId));
  }, []);

  useEffect(async () => {
    setIsLoadingItemList(true);
    if (selectedCollection) {
      dispatch(getitemList(selectedCollection))
        .then(() => {
          setIsLoadingItemList(false); // Clear loading state when item list is fetched
        })
        .catch((error) => {
          console.error("Error fetching item list:", error);
          setIsLoadingItemList(false); // Clear loading state on error
        });
    }
  }, [selectedCollection]);

  const handlePageCheck = async (itemId) => {
    let updatedSelectedPages = [];

    // Check if selectedPages is defined and non-empty
    if (Array.isArray(selectedPages) && selectedPages.length > 0) {
      // If selectedPages is already defined and not empty, create a new array with its contents
      updatedSelectedPages = [...selectedPages];
    }

    if (updatedSelectedPages.includes(itemId)) {
      // If itemId is in the array, remove it
      updatedSelectedPages = updatedSelectedPages.filter(
        (item) => item !== itemId
      );
    } else {
      // If itemId is not in the array, add it
      updatedSelectedPages.push(itemId);
    }

    // Set the updated array as the new state for selectedPages
    setSelectedPages(updatedSelectedPages);
  };

  useEffect(() => {
    const newBrandOptions = domainList?.map((item) => ({
      value: item._id,
      label: item.name,
    }));
    setDomain(newBrandOptions);
  }, [domainList]);

  const handleItemCheck = async (itemId, collectionId) => {
    let updatedSelectedItem = [];

    // Check if selectedItem is defined and non-empty
    if (Array.isArray(selectedItem) && selectedItem.length > 0) {
      // If selectedItem is already defined and not empty, create a new array with its contents
      updatedSelectedItem = [...selectedItem];
    }

    const itemIndex = updatedSelectedItem.findIndex(
      (item) => item.itemId === itemId && item.collectionId === collectionId
    );

    if (itemIndex !== -1) {
      updatedSelectedItem.splice(itemIndex, 1);
    } else {
      updatedSelectedItem.push({ itemId, collectionId });
    }

    // Set the updated array as the new state for selectedItem
    setSelectedItem(updatedSelectedItem);
  };

  const handleSelectAllPages = () => {
    if (selectedPages?.length === pageList?.length) {
      // Deselect all pages
      setSelectedPages([]);
    } else {
      // Select all pages
      setSelectedPages(pageList);
    }
  };

  const handleSelectAllCollections = () => {
    if (selectedCollection?.length === collectionList?.collections?.length) {
      // Deselect all collections
      setSelectedCollection([]);
    } else {
      // Select all collections
      const allCollectionIds = collectionList?.collections?.map(
        (item) => item._id
      );
      setSelectedCollection(allCollectionIds);
    }
  };

  // useEffect(() => {
  //   dispatch(getitemList(selectedCollection));
  // }, [selectedCollection]);

  return (
    <>
      {isDomainListEmpty && (
        <p style={{ color: "red" }}>Site is not published yet.</p>
      )}
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-between",
        }}
      >
        <Select
          placeholder={"Select a domain"}
          options={domain}
          value={selectedDomain}
          onChange={(selectedOption) => handleDomainCheck(selectedOption)}
          isClearable={true}
          isSearchable={true}
          getOptionLabel={(option) => option.label} // Show domain names
          getOptionValue={(option) => option.value} // Use domain IDs as values
          styles={{
            container: (provided) => ({
              ...provided,
              width: "40%",
              marginBottom: "20px",
            }),
          }}
        />

        <Button
          key="reset"
          onClick={handleReset}
          color="inherit"
          variant="outlined"
          size="small"
          sx={{
            // m: 2.5,
            marginBottom: "20px",
            marginRight: "20px",
            flexShrink: 0,
            // float: "right",
            color: "white",
            bgcolor: "blue",
            "&:hover": {
              borderColor: "lightblue",
              bgcolor: "lightblue",
            },
          }}
        >
          Reset
        </Button>
      </div>

      <div style={containerWrapperStyle}>
        <div style={{ ...containerStyle, ...smallWidthStyle }}>
          <div>
            <div
              style={{ ...headingStyle, display: "flex", alignItems: "center" }}
            >
              <h2 style={{ flex: 1 }}>Page List</h2>
              <div>
                <input
                  type="checkbox"
                  checked={
                    selectedPages?.length > 0
                      ? (pageList || []).every((page) =>
                          (selectedPages || []).includes(page)
                        )
                      : false
                  }
                  onChange={() => handleSelectAllPages()}
                  style={{ marginTop: "10px", cursor: "pointer" }}
                />
              </div>
            </div>
            <div>
              {selectedDomain ? (
                <SearchBar
                  placeholder="Search by name"
                  value={searchQueryPage}
                  style={{ marginTop: "10px" }}
                  onChange={(e) => setSearchQueryPage(e.target.value)}
                />
              ) : null}
            </div>

            {isLoadingPageList ? (
              <div style={{ ...centeredStyle }}>
                <CircularProgress
                  disabledLink
                  sx={{
                    width: 64,
                    height: 64,
                    margin: "auto",
                    display: "block",
                    marginTop: "90px",
                  }}
                />
              </div>
            ) : (
              <div style={contentStyle}>
                {selectedDomain ? (
                  <ul style={listStyle}>
                    {filteredPageList?.map((item) => {
                      return (
                        <li key={item.id} style={listItemStyle}>
                          <input
                            type="checkbox"
                            checked={selectedPages?.includes(item)}
                            onChange={() => handlePageCheck(item)}
                            style={{
                              marginRight: "10px",
                              marginTop: "10px",
                              cursor: "pointer",
                            }}
                          />
                          {item}
                        </li>
                      );
                    })}
                  </ul>
                ) : (
                  <p style={{ color: "red" }}>Please select a domain.</p>
                )}
              </div>
            )}
          </div>
        </div>
        <div style={{ ...containerStyle, ...smallWidthStyle }}>
          <div>
            <div
              style={{ ...headingStyle, display: "flex", alignItems: "center" }}
            >
              <h2 style={{ flex: 1 }}>Collection List</h2>
              <div>
                <input
                  type="checkbox"
                  checked={
                    selectedCollection?.length ===
                    collectionList?.collections?.length
                  }
                  onChange={() => handleSelectAllCollections()}
                  style={{ marginTop: "10px", cursor: "pointer" }}
                />
              </div>
            </div>
            <div>
              {selectedDomain ? (
                <SearchBar
                  placeholder="Search by name or slug"
                  value={searchQuery}
                  style={{ marginTop: "10px" }}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
              ) : null}
            </div>
            <div style={contentStyle}>
              <ul style={listStyle}>
                {selectedDomain ? (
                  filteredCollectionList?.map((item) => {
                    return (
                      <li
                        key={item.id}
                        style={{
                          ...listItemStyle,
                          display: "flex",
                        }}
                      >
                        <div
                          style={{ display: "flex", alignItems: "flex-start" }}
                        >
                          <input
                            type="checkbox"
                            checked={selectedCollection?.includes(item._id)}
                            onChange={() => handleCollectionCheck(item._id)}
                            style={{
                              marginRight: "10px",
                              marginTop: "5px",
                              cursor: "pointer",
                            }}
                          />
                        </div>
                        <div>
                          <DetailsComponent
                            title={item.name}
                            content={item.slug}
                            content2={item.lastUpdated}
                          />
                        </div>
                      </li>
                    );
                  })
                ) : (
                  <p style={{ color: "red" }}>Please select a domain.</p>
                )}
              </ul>
            </div>
          </div>
        </div>
        <div style={{ ...containerStyle, ...bigWidthStyle }}>
          <div>
            <div
              style={{ ...headingStyle, display: "flex", alignItems: "center" }}
            >
              <h2 style={{ flex: 1 }}>Items List</h2>
              <div>
                <input
                  type="checkbox"
                  checked={allItemsSelected}
                  onChange={handleAllSelectedItems}
                  style={{ marginTop: "10px", cursor: "pointer" }}
                />
              </div>
            </div>
            <div>
              {selectedDomain && selectedCollection?.length > 0 ? (
                <SearchBar
                  placeholder="Search by name or slug"
                  value={searchQueryItem}
                  style={{ marginTop: "10px" }}
                  onChange={(e) => setSearchQueryItem(e.target.value)}
                />
              ) : null}
            </div>

            {isLoadingItemList && selectedCollection?.length > 0 ? (
              <div style={{ ...centeredStyle }}>
                <CircularProgress
                  disabledLink
                  sx={{
                    width: 64,
                    height: 64,
                    margin: "auto", // Center the CircularProgress both vertically and horizontally
                    display: "block",
                    marginTop: "90px",
                  }}
                />
              </div>
            ) : (
              // renderItemList()
              <ul style={listStyle}>
                {selectedDomain && selectedCollection?.length > 0 ? (
                  filteredItemList?.map(
                    (item1) => (
                      // item?.map((item1) => (
                      <li key={item1.id} style={listItemStyle}>
                        <div
                          style={{ display: "flex", alignItems: "flex-start" }}
                        >
                          <input
                            type="checkbox"
                            style={{
                              marginRight: "10px",
                              marginTop: "10px",
                              cursor: "pointer",
                            }}
                            checked={selectedItem?.some(
                              (item) =>
                                item.itemId === item1._id &&
                                item.collectionId === item1._cid
                            )}
                            onChange={() =>
                              handleItemCheck(item1._id, item1._cid)
                            }
                          />
                          <div>
                            <DetailsComponent
                              title={
                                <span
                                  style={{
                                    color: item1._archived
                                      ? "green"
                                      : "inherit",
                                  }}
                                >
                                  {item1.name}
                                </span>
                              }
                              content={
                                <span
                                  style={{
                                    color: item1._archived
                                      ? "green"
                                      : "inherit",
                                  }}
                                >
                                  {item1.slug}
                                </span>
                              }
                              content2={
                                <span
                                  style={{
                                    color: item1._archived
                                      ? "green"
                                      : "inherit",
                                  }}
                                >
                                  {formatDate(item1["updated-on"])}
                                </span>
                              }
                              content3={
                                <span
                                  style={{
                                    color: item1._archived
                                      ? "green"
                                      : "inherit",
                                  }}
                                >
                                  {item1["copy-link"]}
                                </span>
                              }
                            />
                          </div>
                        </div>
                      </li>
                    )
                    // ))
                  )
                ) : (
                  <p style={{ color: "red" }}>Please select a collection.</p>
                )}
              </ul>
            )}
          </div>
        </div>
      </div>
      <div>
        <div
          style={{
            display: "flex",
            justifyContent: "flex-end",
            marginBottom: "16px",
          }}
        >
          <LoadingButton
            disabled={
              !selectedDomain || (selectedCollection && !selectedItem?.length)
            }
            color="inherit"
            variant="outlined"
            size="medium"
            onClick={handleGenerateMap}
            loading={isSubmitting}
            sx={{
              m: 1.5,
              mt: 8,
              width: "20%",
              float: "right",
              flexShrink: 0,
              color: "white",
              bgcolor: "blue",
              color: "white",
              "&:hover": {
                borderColor: "lightblue",
                bgcolor: "lightblue",
              },
            }}
          >
            Generate Sitemap
          </LoadingButton>
        </div>
      </div>
    </>
  );
};

export default Collection;
