import { createSlice } from "@reduxjs/toolkit";
// utils
import axios from "../../utils/axios";
//
import { dispatch } from "../store";

// ----------------------------------------------------------------------

const initialState = {
  isLoading: false,
  error: null,
  couponList: [],
  coupon: [],
};

const slice = createSlice({
  name: "coupon",
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    // GET EVENTS
    getCoupons(state, action) {
      state.isLoading = false;
      state.couponList = action.payload;
    },

    getOneCoupon(state, action) {
      state.isLoading = false;
      state.coupon = action.payload;
    },
  },
});

// Reducer
export default slice.reducer;

// Actions
export const { getCoupons } = slice.actions;

// ----------------------------------------------------------------------

export function ListCoupons() {
  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get("api/v1/coupons");
      const coupons = response?.data?.data;
      dispatch(
        slice.actions.getCoupons(
          coupons.sort(
            (a, b) => new Date(b.created_at) - new Date(a.created_at)
          )
        )
      );
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

export function OneCoupon(couponId) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.get(`api/v1/coupons/${couponId}`);
      dispatch(slice.actions.getOneCoupon(response?.data?.data));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function createCoupon(newCoupon) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.post("/api/v1/coupons", newCoupon);
      dispatch(ListCoupons());
      return response;
    } catch (error) {
      console.log({ error });
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function updateCoupon(couponId, updateData) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const response = await axios.put(
        `/api/v1/coupons/${couponId}`,
        updateData
      );
      dispatch(ListCoupons());
      return response;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------

export function deleteCoupon(couponId) {
  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const deletedItem = await axios.delete(`/api/v1/coupons/${couponId}`);
      dispatch(ListCoupons());
      return deletedItem;
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

// ----------------------------------------------------------------------
