import PropTypes from "prop-types";
import * as Yup from "yup";
// form
import { yupResolver } from "@hookform/resolvers/yup";
import { useForm } from "react-hook-form";
// @mui
import { Alert, InputAdornment, Stack, IconButton } from "@mui/material";
import { LoadingButton } from "@mui/lab";
// hooks
import useIsMountedRef from "../../../hooks/useIsMountedRef";
// components
import { FormProvider, RHFTextField } from "../../../components/hook-form";
import { useDispatch } from "react-redux";
import {
  createPassword,
  getforgotPassword,
} from "src/redux/slices/forgotPassword";
import { useState } from "react";
import Iconify from "src/components/Iconify";
import { PATH_AUTH } from "src/routes/paths";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router";

// ----------------------------------------------------------------------

export default function CreatePasswordForm({ onSent, onGetEmail }) {
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const { enqueueSnackbar } = useSnackbar();

  const isMountedRef = useIsMountedRef();
  const dispatch = useDispatch();

  const ResetPasswordSchema = Yup.object().shape({
    password: Yup.string().required("Password is required"),
    confirm_password: Yup.string().required("Confirm Password is required"),
  });

  const methods = useForm({
    resolver: yupResolver(ResetPasswordSchema),
    defaultValues: { password: "", confirm_password: "" },
  });

  const {
    reset,
    setError,
    handleSubmit,
    formState: { errors, isSubmitting },
  } = methods;

  const onSubmit = async (data) => {
    try {
      const res = await dispatch(
        createPassword(data.password, data.confirm_password)
      );
      await new Promise((resolve) => setTimeout(resolve, 500));
      enqueueSnackbar("Password create successfully. Please Login!!");
      navigate(PATH_AUTH.login);
    } catch (error) {
      reset();
      if (isMountedRef.current) {
        setError("afterSubmit", error);
      }
    }
  };

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Stack spacing={3}>
        {!!errors.afterSubmit && (
          <Alert severity="error">{errors.afterSubmit.message}</Alert>
        )}
        <RHFTextField
          name="password"
          label="Password"
          type={showPassword ? "text" : "password"}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  edge="end"
                  onClick={() => setShowPassword(!showPassword)}
                >
                  <Iconify
                    icon={showPassword ? "eva:eye-fill" : "eva:eye-off-fill"}
                  />
                </IconButton>
              </InputAdornment>
            ),
          }}
        />
        <RHFTextField
          name="confirm_password"
          label="Confirm Password"
          type={showConfirmPassword ? "text" : "password"}
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  edge="end"
                  onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                >
                  <Iconify
                    icon={
                      showConfirmPassword ? "eva:eye-fill" : "eva:eye-off-fill"
                    }
                  />
                </IconButton>
              </InputAdornment>
            ),
          }}
        />{" "}
        <LoadingButton
          fullWidth
          size="large"
          type="submit"
          variant="contained"
          loading={isSubmitting}
        >
          Create Password
        </LoadingButton>
      </Stack>
    </FormProvider>
  );
}
