import { createSlice } from "@reduxjs/toolkit";
// utils
import axios from "../../utils/axios";
//
import { dispatch } from "../store";

// ----------------------------------------------------------------------

const initialState = {
  isLoading: false,
  error: null,
  domain: {},
  pageList: [],
  collectionList: [],
  itemList: [],
  sitemap: {},
  draft: {},
};

const slice = createSlice({
  name: "collection",
  initialState,
  reducers: {
    // START LOADING
    startLoading(state) {
      state.isLoading = true;
    },

    // HAS ERROR
    hasError(state, action) {
      state.isLoading = false;
      state.error = action.payload;
    },

    // GET EVENTS
    getCollection(state, action) {
      state.isLoading = false;
      state.collectionList = action.payload;
    },
    getDraft(state, action) {
      state.isLoading = false;
      state.draft = action.payload;
    },
    getDomain(state, action) {
      state.isLoading = false;
      state.domain = action.payload;
    },
    getSitemap(state, action) {
      state.isLoading = false;
      state.sitemap = action.payload;
    },
    getPage(state, action) {
      state.isLoading = false;
      state.pageList = action.payload;
    },
    getItem(state, action) {
      state.isLoading = false;
      state.itemList = action.payload;
    },
  },
});

// Reducer
export default slice.reducer;

// Actions
export const { getCollection } = slice.actions;

// ----------------------------------------------------------------------

export function getDomainList(siteId, token) {
  localStorage.setItem("siteId", siteId);
  localStorage.setItem("token", token);

  return async () => {
    dispatch(slice.actions.startLoading());
    try {
      const config = {
        headers: {
          wfauthorization: token,
        },
      };

      const response = await axios.get(
        `api/v1/site/getSiteDomain?siteId=${siteId}`,
        config // Pass the configuration object with headers
      );
      dispatch(slice.actions.getDomain(response));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

export function getPageList(domainId) {
  const siteId = localStorage.getItem("siteId");
  const token = localStorage.getItem("token");

  return async (dispatch) => {
    dispatch(slice.actions.startLoading());
    try {
      const config = {
        headers: {
          wfauthorization: token,
        },
      };

      const requestBody = {
        siteId: siteId,
        domainId: domainId,
      };

      const response = await axios.post(
        "api/v1/site/getSitePages",
        requestBody,
        config // Pass the configuration object with headers
      );
      dispatch(slice.actions.getPage(response));
    } catch (error) {
      dispatch(slice.actions.hasError(error));
    }
  };
}

