import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { useTheme } from "@mui/material/styles";
import DeleteIcon from "@mui/icons-material/Delete";
import ModeIcon from "@mui/icons-material/Mode";
import { Link as RouterLink, useNavigate } from "react-router-dom";
import Label from "src/components/Label";
import { sentenceCase } from "change-case";
import {
  Card,
  Button,
  Table,
  TableRow,
  TableBody,
  TableCell,
  TableHead,
  IconButton,
  Box,
} from "@mui/material";
import HeaderBreadcrumbs from "src/components/HeaderBreadcrumbs";
import Scrollbar from "src/components/Scrollbar";
import Iconify from "src/components/Iconify";
import { useSnackbar } from "notistack";
import Image from "src/components/Image";
import { PATH_DASHBOARD } from "src/routes/paths";
import { deleteCoupon, ListCoupons } from "src/redux/slices/coupon";
import DeleteConfirmationBox from "src/pages/dashboard/DeleteConfirmation";

export default function AppNewCoupon({ callBack, updateForm }) {
  const theme = useTheme();
  const [rowToDelete, setRowToDelete] = useState(null);
  const [showConfirmationBox, setShowConfirmationBox] = useState(false);
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();
  const navigate = useNavigate();

  useEffect(() => {
    dispatch(ListCoupons());
  }, []);
  const coupons = useSelector((state) => state.coupon.couponList);
  const error = useSelector((state) => state.coupon.error);

  useEffect(() => {
    if (error) {
      enqueueSnackbar(error.message, { variant: "error" });
    }
  }, [error]);

  const deleteCoupons = async (id) => {
    const deletedCoupons = dispatch(deleteCoupon(id)); // typo here
    if (deletedCoupons.status) {
    }
    return deletedCoupons;
  };

  const handleDeleteClick = (id) => {
    setRowToDelete(id);
    setShowConfirmationBox(true);
  };

  const handleDeleteConfirmation = async () => {
    const deletedCoupon = await deleteCoupons(rowToDelete);
    if (deletedCoupon.status) {
      setShowConfirmationBox(false);
      enqueueSnackbar("Coupon successfully deleted!");
    } else {
      setShowConfirmationBox(false);
    }
  };

  return (
    <>
      <Card>
        <HeaderBreadcrumbs
          heading={"Coupon List"}
          links={[]}
          sx={{ m: 2, mt: 1, mb: 0 }}
          action={
            <Button
              sx={{ padding: "6px 9px", fontSize: "12px", minWidth: "10px" }}
              variant="contained"
              startIcon={<Iconify icon={"eva:plus-fill"} />}
              onClick={() => {
                navigate(PATH_DASHBOARD.general.addcoupon, { replace: true });
              }}
            >
              Add Coupon
            </Button>
          }
        />
        <Table>
          <TableHead
            sx={{
              position: "sticky",
              top: 0,
              zIndex: 999,
              background: theme.palette.background.paper,
            }}
          >
            <TableRow>
              <TableCell sx={{ padding: 0.5 }}>Index</TableCell>
              <TableCell sx={{ padding: 0.5 }}>Brand Name</TableCell>
              <TableCell sx={{ padding: 0.5 }}>State</TableCell>
              <TableCell sx={{ padding: 0.5 }}>Cities</TableCell>
              <TableCell sx={{ padding: 0.5 }}>Coupon Image</TableCell>
              <TableCell sx={{ padding: 0.5 }}>Crop Images</TableCell>
              <TableCell sx={{ padding: 0.5 }}>Start Date</TableCell>
              <TableCell sx={{ padding: 0.5 }}>Expiry Date</TableCell>
              <TableCell sx={{ padding: 0.5 }}>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {coupons?.map((row, index) => (
              <TableRow key={row?.id}>
                <TableCell sx={{ padding: 0.5 }}>{index + 1}</TableCell>
                <TableCell sx={{ padding: 0.5 }}>
                  {row?.brand?.brand_name}
                </TableCell>
                <TableCell sx={{ padding: 0.2 }}>{row?.state?.name}</TableCell>
                <TableCell sx={{ whiteSpace: "nowrap", maxWidth: 300 }}>
                  {row?.cities.length <= 5 ? (
                    // Render all cities if there are 5 or fewer
                    row?.cities.map((item) => item.name).join(",")
                  ) : (
                    // Render the first 5 cities followed by a "+X" label for the remaining count
                    <>
                      {row?.cities
                        .slice(0, 5)
                        .map((item) => item.name)
                        .join(",")}
                      ,+{row?.cities.length - 5}
                    </>
                  )}
                </TableCell>
                <TableCell sx={{ padding: 0.5, paddingLeft: 3 }}>
                  <Image
                    src={row?.image}
                    alt="brand"
                    sx={{
                      background: "#d8dad8",
                      border: "1px solid transparent",
                      borderRadius: 1.5,
                      width: "50px",
                      height: "50px",
                      "& .wrapper": {
                        display: "block !important",
                        height: "inherit",
                        "& img": {
                          height: "inherit",
                          minHeight: "inherit",
                        },
                      },
                    }}
                  />
                </TableCell>
                <TableCell sx={{ whiteSpace: "nowrap", maxWidth: 200 }}>
                  <Box gap={1} sx={{ display: "flex", flexWrap: "wrap" }}>
                    {row?.crop_images?.slice(0, 3).map(
                      (item, index) =>
                        item && (
                          <Image
                            src={item?.url}
                            key={index}
                            alt="brand"
                            sx={{
                              background: "#d8dad8",
                              border: "1px solid transparent",
                              borderRadius: 1.5,
                              width: "50px",
                              height: "50px",
                              "& .wrapper": {
                                display: "block !important",
                                height: "inherit",
                                "& img": {
                                  height: "inherit",
                                  minHeight: "inherit",
                                },
                              },
                            }}
                          />
                        )
                    )}
                    {row?.crop_images?.length > 3 && (
                      <Label
                        variant={
                          theme.palette.mode === "light" ? "ghost" : "filled"
                        }
                        color="warning"
                        sx={{
                          fontSize: 10,
                          marginLeft: 1,
                          margin: "auto 0",
                        }}
                      >
                        +{row?.crop_images?.length - 3}
                      </Label>
                    )}
                  </Box>
                </TableCell>
                <TableCell sx={{ padding: 0.5 }}>
                  {new Date(row?.start_date).toLocaleDateString()}
                </TableCell>
                <TableCell sx={{ padding: 0.5 }}>
                  {new Date(row?.expiry_date).toLocaleDateString()}
                </TableCell>
                <TableCell sx={{ padding: 0.5 }}>
                  <IconButton
                    style={{ paddingLeft: 0 }}
                    onClick={() => {
                      navigate(PATH_DASHBOARD.general.updatecoupon(row.id), {
                        replace: true,
                      });
                    }}
                  >
                    <ModeIcon />
                  </IconButton>
                  <IconButton
                    style={{ paddingLeft: 0 }}
                    onClick={() => handleDeleteClick(row.id)}
                  >
                    <DeleteIcon />
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </Card>

      {showConfirmationBox && (
        <DeleteConfirmationBox
          onDelete={() => handleDeleteConfirmation()}
          onCancel={() => setShowConfirmationBox(false)}
          rowToDelete={rowToDelete}
          type="coupon"
        />
      )}
    </>
  );
}
